var React = require( 'react' );

module.exports = class Component extends React.Component {
    constructor( stores, props ) {
        super( props );

        this.stores = stores || [];

        this._beginListening();

        this.state = this.getStateFromStores.bind( this )();
    }

    _beginListening() {
        this.stores
            .forEach( ( store ) => {
                this.update_bound = this.update.bind( this );
                store.beginListening( this.update_bound );
            } );
    }

    componentWillUnmount() {
        this.stores
            .forEach( ( store ) => {
                store.stopListening( this.update_bound );
            } );
    }

    getStateFromStores() {
        console.error( 'Didn\'t implement getStateFromStores' );
        return {};
    }

    update() {
        this.setState( this.getStateFromStores.bind( this )() );
    }
};



