var _ = require( 'underscore' );
console.log( _.functions( _ ) );
var util = require( 'util' );
console.log( _.functions( util ) );

var Observable = require( './Observable' );

class Action extends Observable {
    constructor( event_name ) {
        super( event_name );
    }

    getOnUpdateMethodName() {
        return 'on' + this.event_name;
    }

    invoke( data ) {
        var _this = this;

        if( _this.action ) {
            return _this
                .action( data )
                .then( function( promised_data ) {
                    _this.emitChange( promised_data );
                } )
                .catch( function( err ) {
                    console.error( err );
                } );
        }

        _this.emitChange( data );
    }
}

module.exports = Action;

var all = function() {
    return _
        .chain( this )
        .filter( function( value ) {
            return ( value instanceof Action );
        } )
        .map( function( value ) {
            return value;
        } )
        .value();
};

module.exports.createActions = function createActions( names ) {
    var actions = _
        .reduce( names, function( full, name ) {
            full[ name ] = new Action( name );
            return full;
        }, {} )
    return _.extend( { all }, actions );
}



