var { EventEmitter } = require( 'events' );

class Observable extends EventEmitter {
    constructor( event_name ) {
        super();

        this.event_name = event_name;
    }

    getOnUpdateMethodName() {
        return 'on' + this.event_name + 'Update';
    }

    emitChange( data ) {
        console.log( this.event_name, data, EventEmitter.listenerCount( this, this.event_name ) );
        this.emit( this.event_name, data );
    }

    beginListening( listener ) {
        this.on( this.event_name, listener );
    }

    stopListening( listener ) {
        this.removeListener( this.event_name, listener );
    }
}

module.exports = Observable;



