import _ from 'underscore';

import Store from '../lib/Store';

import RulesStore from './RulesStore';
import ClassesStore from './ClassesStore';

let cards = require( '../data/cards' );

class CardsStore extends Store {
    constructor() {
        super( 'CardsStore', [ RulesStore, ClassesStore ] );
    }

    getInitialState() {
        return {
            cards: cards,
            shown_cards: [],
            deselected_cards: []
        };
    }

    getCardsForClass( class_id ) {
        return _
            .filter( this.get( 'cards' ), ( card ) => {
                return card.class === class_id;
            } );
    }

    getRuleFilter( should_be_selected ) {
        return function( card ) {
            var any_rule_selected = _
                .some( card.rules, ( rule ) => {
                    return RulesStore.isRuleSelected( rule );
                } );

            return should_be_selected === any_rule_selected;
        };
    }

    updateShownCards() {
        var class_cards = this.getCardsForClass( ClassesStore.get( 'selected' ).id );
        var new_shown_cards = _
            // Filter on Rules
            .filter( class_cards, this.getRuleFilter( false ) );

        var new_deselected_cards = _.filter( class_cards, this.getRuleFilter( true ) );

        this.set( 'shown_cards', new_shown_cards );
        this.set( 'deselected_cards', new_deselected_cards, true );
    }

    onClassesStoreUpdate() {
        this.updateShownCards();
    }

    onRulesStoreUpdate() {
        this.updateShownCards();
    }
}

module.exports = new CardsStore();



