var _ = require( 'underscore' );

var Observable = require( './Observable' );

class Observer extends Observable {
    constructor( event_name, observables ) {
        super( event_name );
        this.observeAll( observables );
    }

    createUpdateMethod( observable ) {
        var _this = this;

        return function( data ) {
            var on_update_method = _this[ observable.getOnUpdateMethodName() ];
            if( on_update_method ) {
                return on_update_method.bind( _this )( data );
            } else {
                _this.onUpdate( observable.event_name, data ).bind( _this );
            }
        };
    }

    onUpdate( name) {
        console.error( 'Implement onUpdate or ' + this.getOnUpdateMethodName( name ) );
    }

    observe( observable ) {
        observable.beginListening( this.createUpdateMethod( observable ).bind( this ) );
    }

    observeAll( observables ) {
        observables = _.flatten( observables );
        _.each( observables, this.observe.bind( this ) );
    }
}

module.exports = Observer;



