require( 'normalize.css' );
require( '../styles/App.scss' );

import React from 'react';

import Component from '../lib/Component';

import ClassesStore from '../stores/ClassesStore';
import RulesStore from '../stores/RulesStore';
import CardsStore from '../stores/CardsStore';

import ClassesActions from '../actions/ClassesActions';
import RulesActions from '../actions/RulesActions';

class AppComponent extends Component {
    constructor() {
        super( [ ClassesStore, RulesStore, CardsStore ] );
    }

    getStateFromStores() {
        return {
            cards: CardsStore.get( 'shown_cards' ),
            other_class_cards: CardsStore.get( 'deselected_cards' ),

            rules: RulesStore.get( 'shown_rules' ),

            classes: ClassesStore.get( 'classes' ),
            selected_class: ClassesStore.get( 'selected' )
        };
    }

    render() {
        return (
            <div
                id="app">
                <div
                    id="header">
                    HSSecrets.com
                </div>
                <div
                    className="container">
                    <div
                        className="column">
                        {
                            this.state.classes ? this.state.classes
                                .map( ( class_obj, i ) => {
                                    return (
                                        <div
                                            key={ 'class' + i }
                                            className={ 'card' + ( ( class_obj === this.state.selected_class ) ? ' activated' : ' deactivated' ) }
                                            onClick={ () => ClassesActions.ClassClicked.invoke.bind( ClassesActions.ClassClicked )( class_obj ) }>
                                            { class_obj.name }
                                        </div>
                                    );
                                } )
                                : null
                        }
                    </div>
                    <div
                        className="column">
                        {
                            this.state.rules ? this.state.rules
                                .map( ( rule, i ) => {
                                    return (
                                        <div
                                            key={ 'rule' + i }
                                            className='card'
                                            className={ 'card' + ( ( RulesStore.isRuleSelected( rule.id ) ) ? ' activated' : ' deactivated' ) }
                                            onClick={ () => RulesActions.RuleClicked.invoke.bind( RulesActions.RuleClicked )( rule ) }>
                                            { rule.text }
                                        </div>
                                    );
                                } )
                                : null
                        }
                    </div>
                    <div
                        className="column">
                        {
                            this.state.cards ? this.state.cards
                                .map( ( card, i ) => {
                                    return (
                                        <div
                                            key={ 'card' + i }
                                            className='card'>
                                            { card.name }
                                            <div>
                                                { card.description }
                                            </div>
                                        </div>
                                    );
                                } )
                                : null
                        }
                        {
                            this.state.other_class_cards ? this.state.other_class_cards
                                .map( ( card, i ) => {
                                    return (
                                        <div
                                            key={ 'card' + i }
                                            className='card deactivated'>
                                            { card.name }
                                        </div>
                                    );
                                } )
                                : null
                        }
                    </div>
                </div>
            </div>
        );
    }
}

AppComponent.defaultProps = {
};

module.exports = AppComponent;



