import _ from 'underscore';

import Store from '../lib/Store';

import ClassesStore from './ClassesStore';

import RulesActions from '../actions/RulesActions';

var rules = require( '../data/rules' );

class RulesStore extends Store {
    constructor() {
        super( 'RulesStore', [ RulesActions.all(), ClassesStore ] );
    }

    getInitialState() {
        return {
            rules: rules,
            shown_rules: [],
            selected: []
        };
    }

    isRuleSelected( rule_id ) {
        return _
            .some( this.get( 'selected' ), ( rule_obj ) => {
                return rule_obj.id === rule_id;
            } );
    }

    updateShownRules() {
        var CardsStore = require( './CardsStore' );
        var class_cards = CardsStore.getCardsForClass( ClassesStore.get( 'selected' ).id );
        var shown_rule_ids = {};

        _.each( class_cards, ( card ) => {
            _.each( card.rules, ( rule ) => {
                shown_rule_ids[ rule ] = true;
            } );
        } );

        shown_rule_ids = Object.keys( shown_rule_ids );

        var new_shown_rules = _
            .filter( this.get( 'rules' ), ( rule ) => {
                return _
                    .some( shown_rule_ids, ( shown_rule_id ) => {
                        return shown_rule_id === rule.id;
                    } );
            } );

        this.set( 'shown_rules', new_shown_rules, true );
    }

    onClassesStoreUpdate() {
        this.set( 'selected', [] );
        this.updateShownRules();
    }

    onRuleClicked( selected_rule ) {
        if( this.isRuleSelected( selected_rule.id ) ) {
            var new_selected = this.get( 'selected' );
            new_selected = _.filter( new_selected, ( previously_selected_rule ) => previously_selected_rule !== selected_rule );
            this.set( 'selected', new_selected, true );
        } else {
            this.get( 'selected' ).push( selected_rule )
        }
        this.emitChange();
    }
}

module.exports = new RulesStore();



