import Store from '../lib/Store';

import ClassesActions from '../actions/ClassesActions';
// var ClassesActions = require( '../actions/ClassesActions' );

var classes = [
    {
        id: 'hunter',
        name: 'Hunter'
    },
    {
        id: 'mage',
        name: 'Mage'
    },
    {
        id: 'paladin',
        name: 'Paladin'
    }
];

class ClassesStore extends Store {
    constructor() {
        super( 'ClassesStore', [ ClassesActions.all() ] );
    }

    getInitialState() {
        return {
            classes: classes,
            selected: null
        };
    }

    onClassClicked( class_obj ) {
        console.log( 'Setting Selected' );
        this.set( 'selected', class_obj, true );
        this.emitChange();
    }
}

module.exports = new ClassesStore();



