var Observer = require( './Observer' );

class Store extends Observer {
    constructor( event_name, observables ) {
        super( event_name, observables );

        this.state = this.getInitialState();
    }

    emitChange() {
        super.emitChange( this.state );
    }

    getInitialState() {
        console.error( this.event_name + ' has\'nt defined getInitialState' );
    }

    get( name ) {
        if( !name ) return this.state;
        return this.state[ name ];
    }

    set( name, value, emit ) {
        this.state[ name ] = value;
        if( emit ) this.emitChange();
    }
}

module.exports = Store;



